import { render } from "@testing-library/react";
import React, { ReactNode, useEffect } from "react";
import { ProgressBar, Table } from "react-bootstrap";
import { Link, useSearchParams } from "react-router-dom";
import {
  atom,
  GetCallback,
  GetRecoilValue,
  Loadable,
  RecoilState,
  RecoilValue,
  selector,
  useRecoilState,
  useRecoilValue,
  WrappedValue,
} from "recoil";

export interface SimpleObject {
  [key: string]: any;
}

function shallowEqual(object1: SimpleObject, object2: SimpleObject) {
  const keys1 = Object.keys(object1);
  const keys2 = Object.keys(object2);

  if (keys1.length !== keys2.length) {
    return false;
  }

  for (let key of keys1) {
    if (object1[key] !== object2[key]) {
      return false;
    }
  }

  return true;
}

export function buildQueryStrring(query: SimpleObject) {
  return Object.entries(query)
    .filter(([, value]) => value !== undefined)
    .map(([key, value]) => `${key}=${value}`)
    .join("&")
    ;
}

export type QueryBuilders<Q> = {
  [key in keyof Q]: {
    searchParamName?: string;
    parseSearchParam: (searchParam: string | null) => Q[key],
  }
};

export function buildBuildQuery<Q extends SimpleObject>(builder: QueryBuilders<Q>): BuildQuery<Q> {
  return searchParams => {
    const query: Q = {} as any as Q;
    for (const key in builder) {
      const { searchParamName, parseSearchParam } = builder[key];
      const name = searchParamName || key;
      const searchParam = searchParams.get(name);
      const value = parseSearchParam(searchParam);
      query[key] = value;
    };
    return query;
  };

}

export type BuildQuery<T> = (params: URLSearchParams) => T;

export function useQueryUpdate<T extends SimpleObject>(
  queryState: RecoilState<T>,
  buildQuery: BuildQuery<T>,
) {
  const [searchParam] = useSearchParams();
  const [query, setQuery] = useRecoilState(queryState);
  const $query = buildQuery(searchParam);
  useEffect(() => {
    if (!shallowEqual(query, $query)) {
      setQuery($query);
    }
  }, [query, setQuery, $query]);
}

export interface ItemColumns<T> {
  [key: string]: Render<T> | ReactNode | ItemColumnData<T>;
}

export interface ItemColumn<T> {
  heading: ReactNode | ItemColumnHeading;
  data: Render<T> | ReactNode | ItemColumnData<T>;
}

export interface ItemColumnHeading {
  children: ReactNode;
}

export interface ItemColumnData<T> {
  title?: Render<T, string | undefined> | string | undefined;
  children: Render<T> | ReactNode;
}

export type Render<T, R = ReactNode> = (value: T) => R;

export interface ItemProps<T> {
  state: RecoilValue<T[]>;
  columns: ItemColumn<T>[] | ItemColumns<T>;
}

function renderHeading<T>(expression: ReactNode | { children: ReactNode }): ReactNode {
  if (expression === undefined || expression === null || typeof expression === "boolean" || typeof expression === "number" || typeof expression === "string") {
    return expression;
  } else if ("children" in expression) {
    return expression.children;
  } else {
    return expression;
  }
}

function arrayizeColumns<T>(columns: ItemColumn<T>[] | ItemColumns<T>) {
  if (Array.isArray(columns)) {
    return columns;
  } else {
    return Object.entries(columns).map(([heading, data]) => ({ heading, data }));
  }
}

export function ItemTable<T>(props: ItemProps<T>) {
  const columns = arrayizeColumns(props.columns);
  const page = parseInt(new URLSearchParams(global.location.search).get("page") || "1", 10);
  return (
    <>
      <Table striped bordered hover>
        <thead>
          <tr>
            {columns.map((column, i) => (
              <th key={`th-${i}`}>{renderHeading(column.heading)}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          <React.Suspense fallback={(
            <tr>
              <td colSpan={columns.length}>
                <ProgressBar animated now={100} />
              </td>
            </tr>
          )}>
            <ItemTableBody {...props} />
          </React.Suspense>
        </tbody>
      </Table>

      <nav aria-label="Page navigation example">
        <ul className="pagination">
          {arrayRange(1, 10).map(i => (
            <li className={`page-item ${i === page ? "active" : ""}`} key={`page-${i}`}>
              <Link className="page-link" to={`?page=${i}`}>{i}</Link>
            </li>
          ))}
        </ul>
      </nav>
    </>
  );
}

function rendeData<T, R>(expression: Render<T, R> | R | { children: Render<T, R> | R }): Render<T, R> {

  if (typeof expression === "function") {
    return expression as Render<T, R>;
  } else if (typeof expression === "object" && expression !== null && "children" in expression) {
    return rendeData(expression.children);
  } else {
    return () => expression;
  }

}

function extract<T>(column: ItemColumn<T>, key: Exclude<keyof ItemColumnData<T>, "children">) {
  const data = column.data;
  if (data === undefined || data === null || typeof data === "boolean" || typeof data === "number" || typeof data === "string") {
    return () => undefined;
  } else if (typeof data === "function") {
    return () => undefined;
  } else if ("children" in data) {
    if ("type" in data) {
      return () => undefined;
    } else {
      return rendeData(data[key]);
    }
  } else {
    return () => undefined;
  }
}

export function ItemTableBody<T>({ state, columns: $columns }: ItemProps<T>) {
  const data = useRecoilValue(state);
  const columns = arrayizeColumns($columns);

  return (
    <>
      {data.map((record, i) => (
        <tr key={`tr-${i}`}>
          {columns.map((column, i) => (
            <td key={`td-${i}`} title={rendeData(extract(column, "title"))(record)}>
              {rendeData(column.data)(record)}
            </td>
          ))}
        </tr>
      ))}
    </>
  )
}

const arrayRange = (start: number, stop: number, step: number = 1) =>
  Array.from(
    { length: (stop - start) / step + 1 },
    (value, index) => start + index * step
  );

interface GetOpt<Q> {
  get: GetRecoilValue;
  getCallback: GetCallback;
  queryState: RecoilState<Q>;
}

type Get<Q, T> = (opts: GetOpt<Q>) => Promise<T> | RecoilValue<T> | Loadable<T> | WrappedValue<T> | T;

export function createQueryAndData<Q extends SimpleObject, T>(
  queryStateKey: string,
  builder: QueryBuilders<Q>,
  dataStateKey: string,
  get: Get<Q, T>,
) {

  const buildQuery = buildBuildQuery(builder);

  const queryState = atom({
    key: queryStateKey,
    default: buildQuery(new URLSearchParams(global.location.search)),
  });

  const dataState = selector({
    key: dataStateKey,
    get: opt => get({ ...opt, queryState }),
  });

  function useQueryUpdate() {
    const [searchParam] = useSearchParams();
    const [query, setQuery] = useRecoilState(queryState);
    const $query = buildQuery(searchParam);
    useEffect(() => {
      if (!shallowEqual(query, $query)) {
        setQuery($query);
      }
    }, [query, setQuery, $query]);

  }

  return [useQueryUpdate, dataState, buildQuery, queryState] as const;

}